import React, { useState, useEffect } from 'react';

function App() {
  const [breakLength, setBreakLength] = useState(5)
  const [sessionLength, setSessionLength] = useState(25)
  const [minutes, setMinutes] = useState(25)
  const [seconds, setSeconds] = useState(0)
  const [running, setRunning] = useState(false)
  const [sessionInProgress, setSessionInProgress] = useState(true)

  const handleClick = (event) => {
    if (event.currentTarget.id === "break-decrement") {
      if (breakLength !== 1) {
        setBreakLength(breakLength - 1) 
      }
    } else if (event.currentTarget.id === "break-increment") {
      if (breakLength !== 60) {
        setBreakLength(breakLength + 1) 
      }
    } else if (event.currentTarget.id === "session-decrement") {
      if (sessionLength !== 1) {
        setSessionLength(sessionLength - 1)
        if (!running && sessionInProgress) {
          setMinutes(sessionLength - 1)
          setSeconds(0)
        } 
      }
    } else if (event.currentTarget.id === "session-increment") {
      if (sessionLength !== 60) {
        setSessionLength(sessionLength + 1) 
        if (!running && sessionInProgress) {
          setMinutes(sessionLength + 1)
          setSeconds(0)
        } 
      }
    } else if (event.currentTarget.id === "start_stop") {
        setRunning(!running)
    } else if (event.currentTarget.id === "reset") {
      setBreakLength(5)
      setSessionLength(25)
      setMinutes(25)
      setSeconds(0)
      setRunning(false)
      setSessionInProgress(true)
      document.getElementById("beep").pause()
      document.getElementById("beep").currentTime = 0
    }
  }

  useEffect(() => {
    let interval
    if (running) {
      interval = setInterval(() => {
        if (seconds === 0 && minutes === 0) {
          setSeconds(0)
          setSessionInProgress(!sessionInProgress)
          setMinutes(sessionInProgress ? breakLength : sessionLength)
        } else if (seconds === 1 && minutes === 0) {
          document.getElementById("beep").play()
          setSeconds(prevSeconds => prevSeconds - 1)
        } else if (seconds === 0) {
          setMinutes(prevMinutes => prevMinutes - 1)
          setSeconds(59)
        } else {
          setSeconds(prevSeconds => prevSeconds - 1)
        }
      }, 1000)
    } return () => clearInterval(interval)
  }
  )

  return (
    <div className="App vh-100 d-flex flex-column align-items-center justify-content-center container">
      <h1 className="text-center mb-3">POMODORO CLOCK</h1>
      <div className="row container" style={{maxWidth: 500}}>
        <div className="col-12 col-sm-6 d-flex flex-column align-items-center">
          <h3 className="text-center" id="break-label">Break Length</h3>
          <div className="d-flex justify-content-between" style={{width: 125}}>
            <i className="fa fa-arrow-down fa-2x" onClick={handleClick} id="break-decrement"></i>
            <h3 id="break-length">{breakLength}</h3>
            <i className="fa fa-arrow-up fa-2x" onClick={handleClick} id="break-increment"></i>
          </div>
        </div>
        <div className="col-12 col-sm-6 d-flex flex-column align-items-center">
          <h3 className="text-center" id="break-label">Session Length</h3>
          <div className="d-flex justify-content-between" style={{width: 125}}>
            <i className="fa fa-arrow-down fa-2x" onClick={handleClick} id="session-decrement"></i>
            <h3 id="session-length">{sessionLength}</h3>
            <i className="fa fa-arrow-up fa-2x" onClick={handleClick} id="session-increment"></i>
          </div>
        </div>
      </div>
      <br />
      <div id="sessionBorder" className="d-flex flex-column align-items-center border border-danger rounded p-2 pl-3 pr-3 mb-2" style={{width: 150}}>
        <h2 id="timer-label">{sessionInProgress ? "Session" : "Break"}</h2>
        <h1 id="time-left">
          {minutes.toString().padStart(2, 0)}:{seconds.toString().padStart(2, 0)}
        </h1>
      </div>
      <div className="d-flex justify-content-between" style={{width: 50}}>
        <span id="start_stop" onClick={handleClick}>
          {running ? <i className="fa fa-pause fa-lg"></i> : <i className="fa fa-play fa-lg"></i>}
        </span>
        <span id="reset" onClick={handleClick}>
          <i className="fa fa-refresh fa-lg"></i>
        </span>
      </div>
      <div>
        <audio id="beep" src="./sound/beep.wav"></audio>
      </div>
    </div>
  );
}

export default App;


// User Story #26: When a countdown reaches zero (NOTE: timer MUST reach 00:00), a sound indicating that time is up should play. This should utilize an HTML5 audio tag and have a corresponding id="beep".

// User Story #27: The audio element with id="beep" must be 1 second or longer.

// User Story #28: The audio element with id of beep must stop playing and be rewound to the beginning when the element with the id of reset is clicked.